
import { useRouter } from "vue-router";
import { defineComponent, onMounted, ref, inject } from "vue";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonInput,
  IonCard,
  IonButton,
  IonList,
  IonItem,
  IonLabel,
  IonTitle,
  IonToolbar,
  IonHeader,
  IonMenu,
  IonMenuButton,
  IonButtons,
  menuController,
} from "@ionic/vue";
import { useJuegoStore } from "@/stores/partida";

export default defineComponent({
  name: "ViewNuevoJuego",
  components: {
    IonPage,
    IonContent,
    IonGrid,
    IonRow,
    IonCol,
    IonInput,
    IonCard,
    IonButton,
    IonList,
    IonItem,
    IonLabel,
    IonTitle,
    IonToolbar,
    IonHeader,
    IonMenu,
    IonMenuButton,
    IonButtons,
  },
  setup() {
    const store = useJuegoStore();
    const router = useRouter();
    const inputBusqueda = ref("");
    const muestraAdvertencia = ref(false);
    var USERNAME_MIN_LENGTH = 3;
    const axios = inject("axios") as any;
    var axiosConfig = null as any;

    onMounted(() => {
      if (store.getUsuario) {
        axiosConfig = {
          headers: {
            Authorization: "Bearer " + store.getUsuario.tokenAcceso,
          },
        };
      } else {
        router.push({
          name: "login",
        });
      }
    });
    return {
      USERNAME_MIN_LENGTH,
      muestraAdvertencia,
      inputBusqueda,
      store,
      onClickItem: (jugador: any) => {
        store.addJugadorNuevoJuego({ id: jugador.id, nombre: jugador.nombre });
        store.quitaJugadorBusqueda({ id: jugador.id, nombre: jugador.nombre });
      },
      onClickItemDelete: (jugador: any) => {
        store.quitaJugadorNuevoJuego({
          id: jugador.id,
          nombre: jugador.nombre,
        });
      },
      onChangeBusqueda: (e: any) => {
        e;
        if (inputBusqueda.value.length >= USERNAME_MIN_LENGTH) {
          muestraAdvertencia.value = false;
          axios
            .get("buscaUsuarios/" + inputBusqueda.value, axiosConfig)
            .then((response: any) => {
              store.setBusquedaJugadores(response.data);
            });
        } else {
          muestraAdvertencia.value = true;
        }
      },
      nuevoJuego: () => {
        axios
          .post(
            "/nuevaPartida/",
            {
              usuario: store.getUsuario?.id,
              jugadores: store.getJugadoresNuevoJuego,
            },
            axiosConfig
          )
          .then((response: any) => {
            store.setPartida(response.data);
            router.push({
              name: "juego",
              params: {
                id: response.data.pk,
              },
            });
          });
      },
      onClickItemMenu: (item: any) => {
        menuController.close("menu-id");
        router.push({
          name: item,
        });
      },
    };
  },
});
