import { createRouter, createWebHistory } from "@ionic/vue-router";
import { RouteRecordRaw } from "vue-router";
import ViewMenu from "../views/ViewMenu.vue";
import ViewLogin from "../views/ViewLogin.vue";
import ViewJuego from "../views/ViewJuego.vue";
import ViewNuevoJuego from "../views/ViewNuevoJuego.vue";
import ViewTutorial from "../views/ViewTutorial.vue";
import ViewCargar from "../views/ViewCargar.vue";
import ViewRegistro from "../views/ViewRegistro.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "login",
    component: ViewLogin,
  },
  {
    path: "/registro",
    name: "registro",
    component: ViewRegistro,
  },
  {
    path: "/menu/",
    name: "menu",
    component: ViewMenu,
    children: [
      {
        path: "juego",
        name: "juego",
        component: ViewJuego,
      },
      {
        path: "nuevoJuego",
        name: "nuevoJuego",
        component: ViewNuevoJuego,
      },
      {
        path: "tutorial",
        name: "tutorial",
        component: ViewTutorial,
      },
      {
        path: "cargar",
        name: "cargar",
        component: ViewCargar,
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
