import { defineStore } from "pinia";
import Usuario from "@/models/usuario";
import Jugador from "@/models/jugador";

interface State {
  partida: any | null;
  misPartidas: any | null;
  usuario: Usuario | null;
  busquedaJugador: Jugador[] | any[];
  jugadoresNuevoJuego: Jugador[] | any[];
}

export const useJuegoStore = defineStore("juego", {
  state: (): State => {
    return {
      partida: null,
      usuario: null,
      misPartidas: null,
      busquedaJugador: [],
      jugadoresNuevoJuego: [],
    };
  },
  actions: {
    setUsuario(
      id: string,
      usuario: string,
      token_acceso: string,
      token_refresco: string
    ) {
      const nuevoUsuario: Usuario = {
        id: id,
        nombre: usuario,
        tokenAcceso: token_acceso,
        tokenRefresco: token_refresco,
      };
      this.usuario = nuevoUsuario;
    },
    setBusquedaJugadores(jugadores: Array<unknown>) {
      const busquedaActual: any[] = [];
      jugadores.map((item: any) => {
        const jugador: Jugador = { id: item[0], nombre: item[1] };
        if (
          this.jugadoresNuevoJuego.filter((e) => e.nombre === jugador.nombre)
            .length == 0
        ) {
          busquedaActual.push(jugador);
        }
      });
      this.busquedaJugador = busquedaActual;
    },
    addJugadorNuevoJuego(jugador: Jugador) {
      if (
        this.jugadoresNuevoJuego.filter((e) => e.nombre === jugador.nombre)
          .length > 0
      )
        return;
      this.jugadoresNuevoJuego.push(jugador);
    },
    quitaJugadorBusqueda(jugador: Jugador) {
      this.busquedaJugador = this.busquedaJugador.filter((item) => {
        return item.nombre != jugador.nombre;
      });
    },
    quitaJugadorNuevoJuego(jugador: Jugador) {
      this.jugadoresNuevoJuego = this.jugadoresNuevoJuego.filter((item) => {
        return item.nombre != jugador.nombre;
      });
    },
    setPartida(partida: any) {
      this.partida = partida;
    },
    setMisPartidas(partidas: any) {
      this.misPartidas = partidas;
    },
  },
  getters: {
    getUsuario(state) {
      return state.usuario;
    },
    getBusquedaJugadores(state) {
      return state.busquedaJugador;
    },
    getJugadoresNuevoJuego(state) {
      return state.jugadoresNuevoJuego;
    },
    getPartida(state) {
      return state.partida;
    },
    getMisPartidas(state) {
      return state.misPartidas;
    },
  },
});
