
import { useRouter } from "vue-router";
import { defineComponent, onMounted } from "vue";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonLabel,
  IonItem,
  IonList,
  IonTitle,
  IonToolbar,
  IonHeader,
  IonMenu,
  IonMenuButton,
  IonButtons,
  IonButton,
  menuController,
} from "@ionic/vue";
import { useJuegoStore } from "@/stores/partida";

export default defineComponent({
  name: "ViewTutorial",
  components: {
    IonPage,
    IonContent,
    IonGrid,
    IonRow,
    IonCol,
    IonLabel,
    IonItem,
    IonList,
    IonTitle,
    IonToolbar,
    IonHeader,
    IonMenu,
    IonMenuButton,
    IonButtons,
    IonButton,
  },
  setup() {
    const store = useJuegoStore();
    const router = useRouter();

    onMounted(() => {
      if (store.getUsuario) {
        1;
      } else {
        router.push({
          name: "login",
        });
      }
    });
    return {
      onClickItemMenu: (item: any) => {
        menuController.close("menu-id");
        router.push({
          name: item,
        });
      },
    };
  },
});
