
import { useRouter } from "vue-router";
import { defineComponent, onMounted, inject, ref } from "vue";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonCard,
  IonList,
  IonItem,
  IonLabel,
  IonAccordionGroup,
  IonAccordion,
  IonRefresher,
  IonRefresherContent,
  IonButton,
  IonBadge,
  IonToggle,
} from "@ionic/vue";
import { useJuegoStore } from "@/stores/partida";

export default defineComponent({
  name: "ViewCargar",
  components: {
    IonPage,
    IonContent,
    IonGrid,
    IonRow,
    IonCol,
    IonCard,
    IonList,
    IonItem,
    IonLabel,
    IonAccordionGroup,
    IonAccordion,
    IonRefresher,
    IonRefresherContent,
    IonButton,
    IonBadge,
    IonToggle,
  },
  setup() {
    const axios = inject("axios") as any;
    const store = useJuegoStore();
    const router = useRouter();
    const verFinalizadas = ref(false);

    var axiosConfig = store.getUsuario
      ? {
          headers: {
            Authorization: "Bearer " + store.getUsuario.tokenAcceso,
          },
        }
      : (null as any);

    onMounted(() => {
      if (!store.getUsuario) {
        router.push({
          name: "login",
        });
      }
    });
    return {
      store,
      verFinalizadas,
      onClickItem: (item: any) => {
        let idJuego = item.id;
        router.push({
          name: "juego",
          params: { id: idJuego },
        });
      },
      handleRefresh: (event: any) => {
        setTimeout(() => {
          axios.get("/misPartidas/", axiosConfig).then((response: any) => {
            store.setMisPartidas(response.data);
            event.target.complete();
          });
        }, 2000);
      },
      colorItem: (item: any) => {
        if (item.finalizado) {
          return "";
        } else {
          return store.getUsuario &&
            store.getUsuario.nombre == item.usuarioTurno
            ? "warning"
            : "light";
        }
      },
      claseCasilla: (casilla: any) => {
        return casilla.ficha ? "ficha" : "";
      },
      mostrar: (item: any) => {
        if (!item.finalizado) return true;
        return verFinalizadas.value;
      },
      cambiaVerFinalizadas: () => {
        verFinalizadas.value = !verFinalizadas.value;
      },
    };
  },
});
