
import { defineComponent, inject, ref } from "vue";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonInput,
  IonButton,
  IonList,
  IonItem,
  IonLabel,
  IonCard,
  IonCardTitle,
  IonCardSubtitle,
  IonCardHeader,
  IonImg,
  IonThumbnail,
} from "@ionic/vue";

import { useRouter } from "vue-router";
import { useJuegoStore } from "@/stores/partida";

export default defineComponent({
  name: "ViewLogin",
  components: {
    IonPage,
    IonContent,
    IonGrid,
    IonRow,
    IonCol,
    IonInput,
    IonButton,
    IonList,
    IonItem,
    IonLabel,
    IonCard,
    IonCardTitle,
    IonCardSubtitle,
    IonCardHeader,
    IonImg,
    IonThumbnail,
  },
  setup() {
    const store = useJuegoStore();
    const router = useRouter();
    const usuario = ref("");
    const contrasena = ref("");
    const error = ref(false);
    const axios = inject("axios") as any;

    return {
      usuario,
      contrasena,
      error,
      login: () => {
        error.value = false;
        axios
          .post("/login/", {
            u: usuario.value,
            p: contrasena.value,
          })
          .then((response: any) => {
            if ("error" in response.data) {
              error.value = true;
              return;
            }
            store.setUsuario(
              response.data.id,
              response.data.user,
              response.data.access_token,
              response.data.refresh_token
            );
            store.setMisPartidas(response.data.partidas);
            router.push({
              path: "/menu/cargar",
            });
          });
      },
      registro: () => {
        router.push({
          path: "/registro",
        });
      },
    };
  },
});
