import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";

import { IonicVue } from "@ionic/vue";

/* Core CSS required for Ionic components to work properly */
import "@ionic/vue/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/vue/css/normalize.css";
import "@ionic/vue/css/structure.css";
import "@ionic/vue/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/vue/css/padding.css";
import "@ionic/vue/css/float-elements.css";
import "@ionic/vue/css/text-alignment.css";
import "@ionic/vue/css/text-transformation.css";
import "@ionic/vue/css/flex-utils.css";
import "@ionic/vue/css/display.css";
import axios from "axios";

/* Theme variables */
import "./theme/variables.css";

import "./main.css";
import { createPinia } from "pinia";

const axios_ = axios.create();
axios_.defaults.xsrfHeaderName = "X-CSRFTOKEN";
axios_.defaults.xsrfCookieName = "csrftoken";
//axios_.defaults.headers.post["Content-Type"] = "application/json;charset=utf-8";
axios_.defaults.baseURL = "https://proyectouniacc.eastus.cloudapp.azure.com/";
//axios_.defaults.baseURL = "http://localhost:8000/";

const pinia = createPinia();
const app = createApp(App).use(IonicVue).use(router).use(pinia);
app.provide("pinia", pinia);
app.provide("axios", axios_);

router.isReady().then(() => {
  app.mount("#app");
});
