
import { useRouter } from "vue-router";
import { defineComponent, onMounted } from "vue";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonList,
  IonButton,
  IonItem,
  IonLabel,
  IonTitle,
  IonToolbar,
  IonHeader,
  IonMenu,
  IonMenuButton,
  IonButtons,
  IonBadge,
  IonIcon,
  IonRouterOutlet,
  menuController,
} from "@ionic/vue";
import { useJuegoStore } from "@/stores/partida";

export default defineComponent({
  name: "ViewMenu",
  components: {
    IonPage,
    IonContent,
    IonGrid,
    IonRow,
    IonCol,
    IonList,
    IonButton,
    IonItem,
    IonLabel,
    IonTitle,
    IonToolbar,
    IonHeader,
    IonMenu,
    IonMenuButton,
    IonButtons,
    IonBadge,
    IonIcon,
    IonRouterOutlet,
  },
  setup() {
    const store = useJuegoStore();
    const router = useRouter();
    const ultimaPartida =
      store.getMisPartidas.length > 0 ? store.getMisPartidas[0] : null;

    onMounted(() => {
      if (store.getUsuario) {
        1;
      } else {
        router.push({
          name: "login",
        });
      }
    });

    return {
      ultimaPartida,
      onClickItemMenu: (item: any) => {
        menuController.close("menu-id");
        router.push({
          name: item,
        });
      },
    };
  },
});
