
import { defineComponent, inject, ref } from "vue";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonInput,
  IonButton,
  IonList,
  IonItem,
  IonLabel,
  IonCard,
  IonCardTitle,
  IonCardSubtitle,
  IonCardHeader,
  IonImg,
  IonThumbnail,
} from "@ionic/vue";

export default defineComponent({
  name: "ViewRegistro",
  components: {
    IonPage,
    IonContent,
    IonGrid,
    IonRow,
    IonCol,
    IonInput,
    IonButton,
    IonList,
    IonItem,
    IonLabel,
    IonCard,
    IonCardTitle,
    IonCardSubtitle,
    IonCardHeader,
    IonImg,
    IonThumbnail,
  },
  setup() {
    const usuario = ref("");
    const contrasena = ref("");
    const contrasena2 = ref("");
    const correo = ref("");
    const mensaje = ref("");
    const error = ref(false);
    const desactiva = ref(false);
    const axios = inject("axios") as any;

    return {
      usuario,
      contrasena,
      contrasena2,
      correo,
      error,
      mensaje,
      desactiva,
      creaUsuario: () => {
        if (contrasena.value != contrasena2.value) {
          mensaje.value = "Las contraseÃ±as deben ser iguales";
          return;
        }
        error.value = false;
        axios
          .post("/registro/", {
            u: usuario.value,
            c: correo.value,
            p: contrasena.value,
            p2: contrasena2.value,
          })
          .then((response: any) => {
            console.log(response.data);
            if ("error" in response.data) {
              mensaje.value = response.data.error;
              return;
            }
            desactiva.value = true;
            mensaje.value =
              "Usuarix creadx. Vuelve al inicio e intenta ingresar con tu nuevo usuario y contraseÃ±a :-)";
          });
      },
      inputPassCambia: () => {
        if (contrasena.value != contrasena2.value) {
          mensaje.value = "Las contraseÃ±as deben ser iguales";
        } else {
          mensaje.value = "";
        }
      },
    };
  },
});
