
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import {
  IonPage,
  IonContent,
  IonButton,
  onIonViewWillEnter,
  onIonViewDidEnter,
  onIonViewWillLeave,
  onIonViewDidLeave,
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
} from "@ionic/vue";
import PhaserContainer from "@/components/PhaserContainer.vue";
import { useJuegoStore } from "@/stores/partida";

export default defineComponent({
  name: "ViewJuego",
  components: {
    IonPage,
    IonContent,
    PhaserContainer,
    IonButton,
    IonCard,
    IonCardContent,
    IonCardHeader,
    IonCardSubtitle,
    IonCardTitle,
  },
  setup() {
    const store = useJuegoStore();
    const router = useRouter();
    const route = useRoute();
    const partida_id = ref("");
    const childComponentRef = ref();
    const counter = ref(0);

    onMounted(() => {
      if (!store.getUsuario) {
        router.push({
          name: "login",
        });
      }
    });

    onIonViewDidEnter(() => {
      partida_id.value = route.params["id"] as string;
      counter.value = counter.value + 1;
      if (counter.value == 999) counter.value = 0;
    });

    onIonViewDidLeave(() => {
      console.log("view juego did leave");
      childComponentRef.value.metodo();
    });

    onIonViewWillEnter(() => {
      console.log("view juego will enter");
    });

    onIonViewWillLeave(() => {
      console.log("view juego will leave");
    });

    return {
      PhaserContainer,
      store,
      partida_id,
      childComponentRef,
      counter,
    };
  },
});
